.myLogo {
  display: flex;
  flex-direction: column;
  border: 2px solid black;
  margin: 1rem 0;
  text-align: left;
  padding: 0 1rem;
  align-items: center;
  margin-right: auto;
  margin-left: 10rem;
}

* {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0px;
  padding: 0px;
}
.logo-container ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-block;
}
.logo-container ul li {
  width: 300px;
  height: 120px;
  background: #fff;
  border-radius: 10px;
  margin: 10px;
  float: left;
  padding: 20px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo-container ul li a {
  text-decoration: none !important;
  display: inline-block;
}
.logoHolder {
  text-align: center;
  width: 100%;
  position: relative;
  float: left;
}
/* Logo-1 */
.logoHolder > h3 {
  color: #e74c3c;
  font-family: "Oswald", sans-serif;
  font-weight: 300;
  font-size: 50px;
  line-height: 1.3;
}
.logoHolder > p {
  font-size: 14px;
  letter-spacing: 8px;
  text-transform: uppercase;
  padding-left: 10px;
  color: #34495e;
  font-weight: 600;
}

@media only screen and (max-width: 1200px) {
  body {
    margin: 0;
    padding: 0;
  }
  .logo-container ul {
    width: 100%;
    text-align: center;
  }
  .logo-container ul li {
    width: 290px;
    margin-left: auto;
    margin-right: auto;
    float: none !important;
  }
  .logo-4 p {
    margin-top: 2px;
  }
  .Logos {
    margin-top: 20px;
  }

  .myLogo {
    margin: 0;
  }
}

h1.Logos {
  font-weight: 400;
  font-family: "Bangers", cursive;
  font-size: 40px;
  text-align: center;
  letter-spacing: 3px;
  text-shadow: 2px 2px 0px #2d303a, -2px -2px 0px #2d303a, -2px 2px 0px #2d303a,
    2px -2px 0px #2d303a;
  color: #fff;
}
p.para {
  text-align: center;
  font-size: 16px;
  margin-bottom: 20px;
  letter-spacing: 30px;
  font-family: "Lora", serif;
  padding-top: 5px;
  color: #333333;
}
