.container {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  margin-right: 10rem;
  margin-left: 10rem;
  /* padding-right: 2rem;
  padding-left: 2rem; */
  margin-top: 5rem;
  /* box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); */
}

.container > img {
  width: 100%;
  border-radius: 5px;
}

.cards {
  padding-top: 12vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-bottom: auto;
  justify-content: space-between;
}

@media only screen and (max-width: 1200px) {
  .cards {
    flex-direction: column;
    align-content: center;
    align-items: center;
    justify-content: space-around;
    margin-top: 2rem;
  }

  .container {
    align-content: center;
    box-shadow: none;
    margin: 5rem 2rem;
  }

  .card {
    width: 100%;
  }
}
