.navbar {
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #14a78c;
}

.link {
  margin-left: auto;
}

/* .navbar ul li {
  list-style: none;
  display: inline;
  margin: 0 5rem;
  margin-left: auto;
  font-size: x-large;
  font-family: "Lora";
  font-weight: bold;
} */

.link ul li {
  list-style: none;
  display: inline;
  margin: 0 5rem;
  font-size: x-large;
  font-family: "Lora";
  font-weight: bold;
}

.link a:hover {
  color: #2f4353;
}

.navbar ul li a {
  text-decoration: none !important;
  color: inherit;
}

@media screen and (max-width: 1200px) {
  /* .navbar {position: relative;} */
  /* .navbar a {
    float: none;
    display: block;
    text-align: left;
  } */
  .link {
    margin: auto;
  }

  .navbar {
    flex-direction: column;
    padding-top: 2rem;
  }
  /* .link {
    margin: auto;
  } */
  .link a {
    float: none;
    display: block;
  }
}
