.container {
  display: flex;
  flex-direction: row;
  align-self: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.item {
  text-align: center;
  word-wrap: normal;
  width: 33%;
  flex: 0 0 30%;
}

.item > h1 {
  font-size: 24px;
  font-weight: bold;
  color: #ececec;
}

.item > p {
  font-size: 18px;
  padding: 0;
  color: #d8d8d8;
  line-height: 1.7;
  margin-top: 2rem;
}

.smallParagraph {
  text-align: center;
}

@media only screen and (max-width: 1200px) {
  .container {
    flex-direction: column;
  }
  .item {
    width: auto;
    margin-top: 2rem;
  }
}
